import PhoneFieldsElement from '~components/country-code/js/generatePhoneFields';
import initI18n from '~components/i18n/i18next';
import { isSmallScreen } from '~global-js/helpers/breakpoints';
import customEvents from '~global/variables/custom-events.json';
import document from 'global/document';
import './css/phone-number.scss';

let i18next = null;

function generateFields() {
  initI18n().then(() => {
    i18next = window.hurricane.i18next;
    let components = setup();
    if (!isSmallScreen()) {
      document.addEventListener('click', (event) => {
        const target = event.target;
        if (!target.closest('.country-code__wrapper')) {
          const numberFields = [].slice.call(document.querySelectorAll('.phone-numbers'));
          numberFields.forEach((numberField) => {
            numberField.classList.remove('show');
          });
        }
      });
    }

    document.addEventListener(customEvents.setupCountryCodeTriggers.name, () => {
      if (components.length > 0) {
        [].slice.call(components).map((instance) => {
          instance.destroy();
          return true;
        });
      }
      components = setup();
    });
  });
}

function setup() {
  const phoneFields = [].slice.call(document.querySelectorAll('.js-input-tel'));
  const components = [];

  if (phoneFields.length > 0) {
    phoneFields.forEach((field) => {
      const element = new PhoneFieldsElement(field, { i18n: i18next });
      element.init();
      components.push(element);
    });
  }

  return components;
}

if (document.readyState !== 'loading') {
  generateFields();
} else {
  document.addEventListener('DOMContentLoaded', generateFields);
}




