import { phoneCodes } from '~global-js/helpers/phoneCodes';
import BaseComponent from '~global-js/component/base-component';
import { isSmallScreen } from '~global-js/helpers/breakpoints';
import customEvents from '~global/variables/custom-events.json';
import { fireCustomEventOnElement } from '~global-js/helpers/custom-events';
import { u } from 'umbrellajs/umbrella';
import countryCodeTemplate from '~components/country-code/templates/country-code.hbs';

export default class PhoneFieldsElement extends BaseComponent {
  constructor(element, props = {}) {
    super(element, props);
  }

  init() {
    this.prepareCountryCodes();
    this.addSelectListener();
  }

  static formatData(data, format) {
    let formattedData = '';
    format.forEach((item) => {
      if (item !== 'code') {
        formattedData += `${data[item]} `;
      } else {
        formattedData += `(${data[item]}) `;
      }
    });

    return formattedData.trim();
  }

  prepareCountryCodes() {
    // Add phone number
    // Insert input field which displays the selected country code
    let fieldName = this.element.getAttribute('name');
    fieldName = fieldName.replace(/[^\w\s]/gi, '');
    const name = `${fieldName}-countryCode`;
    const language = PhoneFieldsElement.getCustomerLocation();
    const fieldSettings = JSON.parse(this.element.getAttribute('data-field-setting'));

    // settings
    let prefixedValue = null;
    let selectValue = ['code'];
    let selectLabel = ['code', 'label'];
    let selectedField = ['code'];
    let currentSelected = PhoneFieldsElement.formatData(phoneCodes[0], selectedField);

    if (fieldSettings) {
      prefixedValue = (fieldSettings.prefixed !== undefined && fieldSettings.prefixed) ?
          fieldSettings.prefixed : prefixedValue;
      selectValue = (fieldSettings.value !== undefined && fieldSettings.value) ?
          fieldSettings.value : selectValue;
      selectLabel = (fieldSettings.label !== undefined && fieldSettings.label) ?
          fieldSettings.label : selectLabel;
      selectedField = (fieldSettings.selected !== undefined && fieldSettings.selected) ?
          fieldSettings.selected : selectedField;
    }

    if (!prefixedValue) {
      phoneCodes.forEach((country) => {
        country.label = this.props.i18n.t(country.label); // eslint-disable-line no-param-reassign
        const iso2Code = country.iso2Code;
        if (iso2Code === language) {
          currentSelected = PhoneFieldsElement.formatData(country, selectedField);
        }
      });
    } else {
      currentSelected = prefixedValue;

      phoneCodes.forEach((country) => {
        country.label = this.props.i18n.t(country.label); // eslint-disable-line no-param-reassign
      });
    }

    const content = countryCodeTemplate({
      countries: phoneCodes,
      language,
      isMobile: isSmallScreen(),
      selectedFieldName: name,
      currentSelected,
      selectValue,
      selectLabel,
      style: (fieldSettings && fieldSettings.style) || 'bottom',
    });
    u(this.element).before(content);

    if (!isSmallScreen()) {
      this.element.parentNode.addEventListener('click', this.toggleCountryCodeClass);
    } else if (this.element.parentNode.querySelector('.phone-numbers')) {
      this.element.parentNode.querySelector('.phone-numbers').value = currentSelected;
    }
  }

  toggleCountryCodeClass = (event) => {
    const target = event.target;
    if (target.closest('.country-code__wrapper') && target.nextElementSibling
      && target.nextElementSibling.nextElementSibling === this.element) {
      target.nextElementSibling.classList.toggle('show');
    }
  }

  static getCustomerLocation() {
    let location;
    let locationContent = window.hurricane ? window.hurricane.clientInfo : null;

    if (locationContent && locationContent.country) {
      location = locationContent.country.toLowerCase();
    } else {
      locationContent = document.documentElement.getAttribute('lang');
      location = locationContent.substring(locationContent.indexOf('-') + 1, locationContent.length);
    }

    return location;
  }

  addSelectListener() {
    const selectedField = this.element.parentElement.querySelector('.selected-phone-number');
    if (isSmallScreen()) {
      const element = this.element.parentElement.querySelector('.phone-numbers');
      element.addEventListener('change', () => {
        PhoneFieldsElement.selectCountry(element.value, selectedField);
      });
    } else {
      const elements = [].slice.call(this.element.parentElement.querySelectorAll('.phone-number'));
      elements.forEach((element) => {
        element.addEventListener('click', () => {
          PhoneFieldsElement.selectCountry(element.getAttribute('value'), selectedField);
        });
      });
    }
  }

  static selectCountry(value, target) {
    target.setAttribute('value', value);
    fireCustomEventOnElement(target, 'change');
    fireCustomEventOnElement(target.parentNode, customEvents.countryCodeChanged.name, value);
    const phoneFileds = [].slice.call(document.querySelectorAll('.phone-numbers'));
    phoneFileds.forEach((field) => {
      field.classList.remove('show');
    });
  }

  destroy() {
    const wrapper = this.element.closest('.country-code__wrapper');

    if (!wrapper) {
      return false;
    }

    if (wrapper.querySelector('.selected-phone-number')) {
      wrapper.removeChild(wrapper.querySelector('.selected-phone-number'));
    }

    if (wrapper.querySelector('.phone-numbers')) {
      wrapper.removeChild(wrapper.querySelector('.phone-numbers'));
    }

    if (!isSmallScreen()) {
      wrapper.removeEventListener('click', this.toggleCountryCodeClass);
    }
    return true;
  }
}



